import React, { Component } from 'react'
import { Link } from "react-router-dom"

export default class menuIcon extends Component {
    constructor(props){
        super(props);
        this.state={
            iconStyle: "menuIcon",
            height: 0,
            visible: "hidden"
        }
        this.toggle=this.toggle.bind(this);
    }

    toggle() {
        if(this.state.iconStyle==="menuIcon"){
            this.setState({
                iconStyle: "change menuIcon",
                height: "auto",
                visible: "visible"
            })
        }else{
            this.setState({
                iconStyle: "menuIcon",
                height: 0,
                visible: "hidden"
            })
        }
    }

    componentDidMount() {
        window.addEventListener("scroll", (e)=>{
            if(this.state.iconStyle!=="menuIcon"){
                this.setState({
                    iconStyle: "menuIcon",
                    height: 0,
                    visible: "hidden"
                })
            }
        });
    }

  render() {
    return (
      <div>
         <div className={this.state.iconStyle} onClick={(e)=>this.toggle(e)}>
            <div className="bar1"></div>
            <div className="bar2"></div>
            <div className="bar3"></div>
        </div>
        <div className="slideMenu" style={{"height":this.state.height}} onClick={(e)=>this.toggle(e)}>
            <ul style={{"visibility":this.state.visible}}>
                <li><Link to={"/"}><div>Home</div></Link></li>
                <li><Link to={"/projects"}><div>Projects</div></Link></li>
                <li><a href="#contact"><div>Contact</div></a></li>
            </ul>
        </div>
      </div>
    )
  }
}
