import React, { Component } from 'react'

export default class projects extends Component {
  constructor(props){
    super(props);
    this.openCity=this.openCity.bind(this);
  }

  openCity(evt, text) {
    console.log(evt)
    var i, tabcontent, tablinks;
    tabcontent = document.getElementsByClassName("tabcontent");
    for (i = 0; i < tabcontent.length; i++) {
      tabcontent[i].style.display = "none";
    }
    tablinks = document.getElementsByClassName("tablinks");
    for (i = 0; i < tablinks.length; i++) {
      tablinks[i].className = tablinks[i].className.replace(" active", "");
    }
    document.getElementById(text).style.display = "block";
    evt.target.className += " active";
  }

  render() {
    return (

      <div className="homeContainer">
        <div className="home">
          <div className="homeCover"></div>
          <div className="legendContent">
            <div className="projects">
              <h1>Projects</h1>
              <ul className="nav nav-pills mb-3" id="pills-tab" role="tablist">
                <li className="nav-item">
                  <a className="nav-link active" id="pills-home-tab" data-toggle="pill" href="#pills-home" role="tab" aria-controls="pills-home" aria-selected="true">Websites/Web Apps</a>
                </li>
                <li className="nav-item">
                  <a className="nav-link" id="pills-contact-tab" data-toggle="pill" href="#pills-contact" role="tab" aria-controls="pills-contact" aria-selected="false">Mobile Apps</a>
                </li>
              </ul>
              <div className="tab-content" id="pills-tabContent">
                <div className="tab-pane fade show active" id="pills-home" role="tabpanel" aria-labelledby="pills-home-tab">No Projects</div>
                <div className="tab-pane fade" id="pills-profile" role="tabpanel" aria-labelledby="pills-profile-tab">No Projects</div>
                <div className="tab-pane fade" id="pills-contact" role="tabpanel" aria-labelledby="pills-contact-tab">No Projects</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      
    )
  }
}
